



































































import {
  Alert,
  AlertAction,
  AlertActionOnType,
  CreateAlertActionRequest,
  CreateAlertForUserRequest,
  WatchedItem
} from '@/models';
import Vue, { PropType } from 'vue';
import { cloneDeep } from 'lodash';

export default Vue.extend({
  name: 'AlertDesigner',

  props: {
    value: Boolean,
    watchedItem: {
      type: Object as PropType<WatchedItem>,
      required: false
    },
    alert: {
      type: Object as PropType<Alert>,
      required: false
    }
  },

  data: () => ({
    nameRules: [(name: string) => !!name || 'Name is required'],
    alertToModify: {} as CreateAlertForUserRequest
  }),

  methods: {
    getActionText(action: AlertAction | CreateAlertActionRequest): string {
      return `When alert ${
        action.actionOn === AlertActionOnType.AlertActivated ? 'activates' : 'deactivates'
      }, send ${action.type.toLowerCase()} to ${action.target}.`;
    }
  },

  watch: {
    alert(): void {
      this.alertToModify = cloneDeep(this.alert) ?? {
        name: '',
        description: '',
        conditions: [],
        actions: []
      };
    }
  },

  computed: {
    show: {
      get(): boolean {
        return this.value;
      },

      set(value: boolean): void {
        this.$emit('input', value);
      }
    }
  }
});
